	module.exports = {"data":{"markdownRemark":{"html":"<p>When running the following program with FlexSDK 3.2, an exception to forced type conversion comes up in the place where Vector instance is created. For the time being, I took notes by asking for a survey after returning home.</p>\n<h2>Source</h2>\n<pre><code class=\"language-ActionScript\">import flash.display.Sprite; \n public class ASTest extends Sprite{ \n  public function ASTest(){ trace( Foo ); } \n } \n public class Foo{ \n  public static var hoge:Vector.&#x3C;Foo> = createHoge(); \n  private static function createHoge():Vector.&#x3C;Foo>{ \n    var vec:Vector.&#x3C;Foo> = new Vector.&#x3C;Foo>(); \n    // ...initializing... \n    return vec; \n  } \n } \n</code></pre>\n<h2>output</h2>\n<pre><code>TypeError: Error #1034: 強制型変換に失敗しました。Vector.&#x3C;*>@13baa19 を __AS3__.vec.Vector.&#x3C;Foo> に変換できません。 \n  at Foo$/createHoge()[C:\\Documents and Settings\\mikuh\\My Documents\\Flex Builder 3\\ASTest\\src\\Foo.as:6] \n  at Foo$cinit() \n  at global$init()[C:\\Documents and Settings\\mikuh\\My Documents\\Flex Builder 3\\ASTest\\src\\Foo.as:3] \n  at ASTest()[C:\\Documents and Settings\\mikuh\\My Documents\\Flex Builder 3\\ASTest\\src\\ASTest.as:5] \n \n [SWF] C:\\Documents and Settings\\mikuh\\My Documents\\Flex Builder 3\\ASTest\\bin-debug\\ASTest.swf - 1,344 バイト (圧縮後) \n</code></pre>","frontmatter":{"cover":null,"date":"2009-01-23T00:00:00+09:00","strDate":"2009/1/22","title":"Is it also a bug in Vector?"}}},"pathContext":{"path":"/en/blog/2009/01/23-vector-class-problem-on-as3/","langKey":"en"}}

